@keyframes moveDots {
  0% {
    background-position: 0 0, 0 0;
  }
  100% {
    background-position: 200% 0, 200% 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(0);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.statsSection {
  position: relative;
  padding: 1rem;
  border-radius: 12px;
  color: #ffffffe8;
  overflow: hidden;
  text-align: center;
  min-height: 200px;
}

/* Blurred background */
.statsSection::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("https://res.cloudinary.com/dmylma7bf/image/upload/v1753383497/top-track-13_rn1cdd.avif");
  background-size: cover;
  background-position: center;
  filter: blur(3px);
  margin: 5px;
  z-index: 0;
}

/* Overlay with dots + subtle linear gradient */
.statsSection::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(201, 198, 198, 0.5);
  background-image: radial-gradient(
      rgba(255, 255, 255, 0.05) 1px,
      transparent 1px
    ),
    linear-gradient(
      120deg,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0.07) 50%,
      rgba(255, 255, 255, 0) 100%
    );
  background-size: 4px 4px, 200% 100%;
  background-repeat: repeat, no-repeat;
  animation: moveDots 220s linear infinite;
  pointer-events: none;
  z-index: 1;
}

/* Foreground content */
.statsSection > * {
  position: relative;
  z-index: 2;
}

/* Title styling */
.statsSection h3 {
  font-size: 2rem;
  font-family: "Exo 2", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #ffffffbd;
  margin-bottom: 1.5rem;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3), 0 0 25px rgba(0, 255, 255, 0.2),
    0 0 50px rgba(0, 255, 255, 0.2);
  animation: fadeIn 1.5s ease-in-out forwards;
  opacity: 0;
}

@media (min-width: 1200px) {
  .statsSection h3 {
    font-size: 1.7rem;
  }
}

/* Stats list */
.statsSection ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.statsSection li {
  font-size: 0.6rem;
  color: #ebeaea;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 5px rgba(200, 255, 255, 0.3);
  animation: fadeIn 1.5s ease-in-out 0.7s forwards;
  opacity: 0;
  margin-bottom: 0.5rem;
  break-inside: avoid;
  text-align: left;
}

/* Bold stat numbers */
.statsSection strong {
  font-size: 0.9rem;
  background: linear-gradient(
    45deg,
    rgba(0, 255, 255, 0.7),
    rgba(255, 0, 255, 0.7)
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 10px rgba(0, 255, 255, 0.3);
}

@media (min-width: 1200px) {
  .statsSection strong {
    font-size: 0.7rem;
  }
}

.statsSection ul {
  list-style: none;
  padding-left: 15px;
  margin: 0;
  columns: 2;
}
